import baseService from './baseService'

export default {

  findUsersByValue: function (filter, callback) {
    return baseService.$.post('/api/Partner/FindUserByValue', {paramValue: filter}, callback);
  },

  getPartners: function (callback) {
    baseService.$.get('/api/Partner/GetUserPartners', function (data) {
      callback(data);
    })
  },
  addPartner: function (username, callback) {
    baseService.$.post('/api/Partner/InsertUserPartner', {'partnerEmail': username}, callback).fail(function (data) {
      alert('could not add partner')
    })
  },

  removePartner: function (username, callback) {
    baseService.$.post('/api/Partner/RemovePartner', {'partnerEmail': username}, callback).fail(function (data) {
      alert('could not remove partner')
    })
  },

  getPartnersByStorageFolder: function (folderId, callback) {
    baseService.$.post('/api/Partner/GetPartnersByStorageFolder', {'storageFolderID': folderId}, callback).fail(function (data) {
      alert('could not get folder partners')
    })
  },

  addPermissionsToPartner: function (folderId, username, permission, callback) {
    baseService.$.post('/api/Partner/InsertUserPartnerPermission', {
      'storageFolderID': folderId,
      'PartnerEmail': username,
      'PermissionTypeID': permission
    }, callback).fail(function (data) {
      alert('could not get folder partners')
    })
  },

  removePermissionsFromPartner: function (folderId, username, callback) {
    baseService.$.post('/api/Partner/RemovePartnerFromFolder', {
      'storageFolderID': folderId,
      'PartnerEmail': username
    }, callback).fail(function (data) {
      alert('could not remove partner from folder')
    })
  }
}



