import baseService from './baseService'

export default {
  getCurrentUserInfo: function (callback) {
    baseService.$.get('/api/account/UserInfo', function (data) {
      data.skills = data.skills.split(';').map(x => Number(x))
      callback(data);
    })
  },

  getUserInfo: function (username, callback) {
    baseService.$.post('/api/Partner/PartnerInfo', {'partnerEmail': username}, function (data) {
      data.skills = data.skills.split(';').map(x => Number(x))
      callback(data);
    })
  },

  uploadProfilePicture: function (file, successCB, failCB) {
    baseService.$.put('/api/Account/UpdateProfileImage', file, function (data) {
      successCB(data);
    }).fail(function (xhr, status, error) {
      failCB(status, baseService.extractParam(xhr, 'error'));
    })
  },

  getSkills(callback) {
    return baseService.$.get('api/Generic/GetSkills', callback);
  },

  updateUserDetails(data, callback) {
    return baseService.$.post('api/Account/UpdateProfile', data, callback);
  }
}



