<template>
  <div class="modal fade" style="display: none;">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-info">
          <button type="button" class="close" data-dismiss="modal"></button>
          <h2 class="modal-title">{{mode}} project</h2>
        </div>

        <div class="modal-body">
          <form id="register-form" class="form-horizontal" @submit.prevent>
            <div class="panel-body">
              <div class="row">
                <fieldset>
                  <div class="form-group">
                    <label class="col-lg-4 control-label">Project Name:</label>
                    <div class="col-lg-7">
                      <input name="email" v-model="project.folderName"
                             class="form-control col-lg-6">
                    </div>
                  </div>

                  <div class="form-group">
                    <label class="col-lg-4 control-label">Project description:</label>
                    <div class="col-lg-7">
                      <textarea name="additional_details" rows="2" cols="5"
                                v-model="project.folderDescription"
                                class="form-control"></textarea>
                    </div>
                  </div>

                </fieldset>
              </div>
            </div>
          </form>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-link" data-dismiss="modal">Close</button>
          <button type="button" class="btn btn-info"
                  @click="$emit('saved', project)" data-dismiss="modal">Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'projectModal',
    props: {
      project: {
        default() {
          return {
            name: '',
            description: '',
            image: '/static/images/demo/flat/15.png'
          }
        }
      },
      mode: {}
    },
    data() {
      return {}
    }
  }
</script>

<style scoped>
  .modal-footer {
    border: none;
  }
</style>



