<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div id="content" class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="page-header-content">
            <div class="page-title">
              <h4><span class="text-semibold">Dashboard</span>
              </h4>
              <a class="heading-elements-toggle"><i class="icon-more"></i></a></div>
          </div>

        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="content">
          <div class="row">
            <div class="col-md-6 no-padding">
              <div style="display: flex">
                <h4 v-if="!freeUser" class="project-list-title">My Projects</h4>
              </div>
            </div>
            <div class="col-md-6 no-padding">
              <h4 class="project-list-title">Shared Projects</h4>
            </div>
          </div>
          <div class="row">
            <div v-if="!freeUser" class="col-md-6 childs-padding-to-margin">
              <ul class="media-list list-hover" style="display: table">
                <li v-for="project in projects" class="media project-li">
                  <div class="panel-body">
                    <div class="media-body">
                      <div class="media-heading">
                        <a href="" class="project-title">{{project.folderName}}</a>
                      </div>
                      {{project.folderDescription}}
                    </div>
                    <div class="media-right media-middle">
                      <ul class="flex">
                        <li class="btn bg-blue-600 btn-rounded btn-icon left"
                            data-popup="tooltip" data-placement="bottom" data-original-title="share"
                            @click="editedProject = clone(project)" data-toggle="modal"
                            data-target="#add_partners_to_project_modal">
                          <i class="icon-share3"></i>
                        </li>

                        <li class="btn bg-blue-600 btn-rounded btn-icon left"
                            data-popup="tooltip" data-placement="bottom" data-original-title="share"
                            @click="editedProject = clone(project)" data-toggle="modal"
                            data-target="#edit_project_modal">
                          <i class="icon-pencil"></i>
                        </li>
                      </ul>
                    </div>
                  </div>
                </li>
              </ul>
            </div>

            <div v-else class="col-md-6">
              <div class="panel panel-body border-top-info">
                <div class="text-center">
                  <h3 class="pull-left no-margin text-semibold free-user-headding">Only paid users can have storage</h3>
                </div>

                <button class="btn btn-success btn-lg" @click="$router.push('/pricing-layout')">
                  upgrade
                </button>
              </div>
            </div>

            <div class="col-md-6 childs-padding-to-margin">
              <ul class="media-list list-hover" style="display: table">
                <li v-for="project in sharedProjects" class="media project-li">
                  <div class="panel-body">
                    <div class="media-body">
                      <div class="media-heading">
                        <a href="" class="project-title">{{project.folderName}}</a>
                      </div>
                      {{project.folderDescription}}
                    </div>
                    <div class="media-right media-middle">
                      <img :src="project.partner.image" class="img-circle img-sm" :alt="project.partner.name"
                           data-toggle="tooltip" data-placement="bottom" :title="project.partner.name">
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div v-if="!freeUser" class="row panel-body" style="padding-bottom: 10px">
            <button type="button" class="btn border-info text-info-600 btn-flat btn-icon mb-10"
                    data-toggle="modal" data-target="#add_project_modal">
              <i class="icon-plus-circle2"></i>
              Add new project
            </button>
          </div>
          <StorageGague></StorageGague>
        </div>
        <!-- /content area -->

      </div>
      <!-- /main content -->

    </div>
    <!-- /page content -->

    <projectModal id="add_project_modal" mode="Add"
                  @saved="addProject($event)"></projectModal>
    <template v-if="!!editedProject">
      <projectModal id="edit_project_modal" mode="Edit"
                    :project="editedProject"
                    @saved="editProject($event)"></projectModal>
      <add-partners-modal id="add_partners_to_project_modal"
                          :addToProject="true"
                          :projectName="editedProject.folderName"
                          :folderId="editedProject.storageFolderID"></add-partners-modal>
    </template>
  </div>
</template>

<script>
  import projectModal from './ProjectModal.vue'
  import dashboardService from '@/services/dashboardService'
  import AddPartnersModal from '@/components/partners/AddPartnersModal.vue'
  import StorageGague from '@/components/user/StorageGague'
  export default {
    name: 'dashboardLayout',
    components: {projectModal, AddPartnersModal, StorageGague},
    data () {
      return {
        projects: [],
        sharedProjects: [{
          id: 1,
          folderName: 'Joe\'s wedding',
          folderDescription: 'this is the Ultimate wedding',
          partner: {
            image: '/static/images/demo/users/face10.jpg',
            name: 'Betzalel '
          }
        }],
        editedProject: null
      }
    },
    created(){
      var self = this;
      dashboardService.getAllMyProject(function (data) {
        self.projects = data;
      })
      dashboardService.getAllMySharedProject(function (data) {
        self.sharedProjects = data;
      })
    },
    computed: {
      freeUser: function () {
        return false;
      }
    },
    methods: {
      clone: function (object) {
        return JSON.parse(JSON.stringify(object))
      },
      addProject: function (project) {
        var self = this;
        dashboardService.createProject(project, function (data) {
          project.storageFolderID = data;
          self.projects.push(project);
        });
      },
      editProject: function (project) {
        this.projects.forEach(function (vProject) {
          if (vProject.storageFolderID === project.storageFolderID) {
            dashboardService.updateFolder(project, function () {
              Object.assign(vProject, project);
            });
          }
        })
      }
    }
  }
</script>

<style scoped>
  .childs-padding-to-margin > div {
  }

  .flex {
    display: flex
  }

  .flex li {
    float: right;
    position: relative;
    display: block;
    margin-left: 3px;
  }

  .project-title {
    font-size: large;
    text-decoration: underline;
  }

  /*.list-striped li:nth-child(even) {*/
  /*background-color: rgba(0, 103, 255, 0.29);*/
  /*}*/
  .project-li {
    border-bottom: 2px solid #cff6ff;
    margin: 0;
    cursor: pointer;
  }

  .project-list-title {
    text-decoration: underline;
  }

  .free-user-headding {
    padding: 8px 20px;
  }
</style>



