import $ from 'jquery';
import Vue from 'vue'

var baseUrl = 'http://79.177.57.124:31291/';
$.ajaxSetup({
  beforeSend: function (xhr, options) {
    options.url = baseUrl + options.url;
    xhr.setRequestHeader('Authorization', 'Bearer ' + Vue.cookie.get('token'));
  },
  crossDomain: true,
  dataType: 'json'
});

$.put = function (url, data, callback, type) {
  // The url can be an options object (which then must have .url)
  return $.ajax($.extend({
    url: url,
    type: 'PUT',
    dataType: type,
    data: data,
    success: callback,
    processData: false,
    contentType: false
  }, $.isPlainObject(url) && url));
}

export default {
  $,
  getMessage(data) {
    return $.parseJSON(data.responseText).message;
  },
  extractParam(data, paramName) {
    return $.parseJSON(data.responseText)[paramName];
  }
}



