import baseService from './baseService'

export default {
  getAllMyProject: function (callback) {
    return baseService.$.post('api/Storage/GetFoldersOwned', {folderParent: null}, callback);
  },

  getAllMySharedProject: function (callback) {
    return baseService.$.post('api/Storage/GetFoldersShared', {folderParent: null}, callback);
  },

  updateFolder: function (folder, callback) {
    folder.folderID = folder.storageFolderID
    return baseService.$.post('api/Storage/UpdateStorageFolder', folder, callback);
  },

  createProject: function (folder, callback) {
    folder.folderParent = null
    return baseService.$.post('api/Storage/CreateStorageFolder', folder, callback);
  }
}



