<template>
  <div class="content ">
    <div class="row">
      <div class="search-component">
        <div class="input-group">
          <input type="text" class="form-control" name="field-2" placeholder="Search a partner..."
                 v-model="partnersFilter">
              <span class="input-group-btn">
                <button class="btn btn-primary" type="button">
                  <span class="glyphicon glyphicon-search"></span>
                </button>
              </span>
        </div>
      </div>
    </div>
    <div class="row">
      <ul class="media-list list-hover">
        <li v-for="partner in filteredUsers" class="media panel-body">
          <div class="media-left">
            <a href="#"><img :src="partner.image" class="img-circle img-md" alt=""></a>
          </div>
          <div class="media-body">
            <div class="media-heading">
              <router-link :to="'/user-profile/' + partner.email">{{partnerFullName(partner)}}</router-link>
            </div>
            <span class="text-muted">{{partner.occupation}}</span>
          </div>
          <div class="pull-right">
            <template v-if="displayPermissionButtons">
              <button type="radio" name="permission" class="btn btn-secondary" @click="removePartner(partner)"
                      :class="{active: !partner.isFriend}">None
              </button>
              <div class="btn-group" role="group" data-toggle="buttons">
                <button type="radio" name="permission" class="btn btn-secondary"
                        :class="{active: (partner.permission === 1)}"
                        @click="addPartner(partner, 1)">View
                </button>
                <button type="radio" name="permission" class="btn btn-secondary"
                        :class="{active: (partner.permission === 2)}"
                        @click="addPartner(partner, 2)">Upload
                </button>
                <button type="radio" name="permission" class="btn btn-secondary"
                        :class="{active: (partner.permission === 3)}"
                        @click="addPartner(partner, 3)">Both
                </button>
              </div>
            </template>
            <button v-else-if="partner.isFriend"
                    @click="removePartner(partner)"
                    class="label label-icon-lg label-success">
              <i class="icon-checkmark4 position-left"></i>
              {{ removePartnerButtonText }}
            </button>
            <button v-else class="btn btn-primary" @click="addPartner(partner)">Add partner</button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import usersService from '@/services/partnersService'
  import Vue from 'Vue'
  export default {
    name: 'partners-searcher',
    props: {
      removePartnerButtonText: {
        type: String,
        default: 'partners'
      },
      partners: {
        type: Array
      },
      displayPermissionButtons: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        users: [],
        partnersFilter: ''
      }
    },
    watch: {
      partners() {
        this.users = this.partners;
        this.getUsers();
      },
      partnersFilter: function () {
        this.getUsers();
      }
    },
    computed: {
      filteredUsers: function () {
        return this.filterUsers(this.users);
      }
    },
    methods: {
      addPartner: function (partner, permission) {
        var self = this;
        this.$emit('addPartner', {username: partner.email, permission}, function () {
          Vue.set(partner, 'isFriend', true);
          if (permission) {
            Vue.set(partner, 'permission', permission);
            self.$forceUpdate()
          }
        })
      },
      removePartner: function (partner) {
        this.$emit('removePartner', {username: partner.email}, function () {
          Vue.set(partner, 'isFriend', false);
        })
      },
      getUsers(){
        var self = this;
        //_debounce
        usersService.findUsersByValue(this.partnersFilter, function (users) {
          self.users = self.arrayUnion(self.users, users.map(function (user) {
            user.isFriend = false;
            user.image = '/static/images/demo/users/face3.jpg';
            return user;
          }), self.usersEqualFunction);
        });
      },
      arrayUnion(arr1, arr2, equalityFunc) {
        if (arr1.length === 0) return arr2;
        if (arr2.length === 0) return arr1;

        var union = arr1.concat(arr2);
        for (var i = 0; i < union.length; i++) {
          for (var j = i + 1; j < union.length; j++) {
            if (equalityFunc(union[i], union[j])) {
              union.splice(j, 1);
              j--;
            }
          }
        }
        return union;
      },
      usersEqualFunction(user1, user2) {
        return user1.email === user2.email;
      },
      filterUsers: function (users) {
        var self = this;
        var partnersFilter = this.partnersFilter.toLowerCase();
        return users.filter(function (partner) {
          return self.partnerFullName(partner).toLowerCase().includes(partnersFilter)
        })
      },
      partnerFullName(partner) {
        return partner.firstName + ' ' + partner.lastName
      }
    }
  }
</script>

<style scoped>
  .search-component {
    border-bottom: 1px solid #00bcd4;
    padding-bottom: 10px;
  }
</style>



