<template>
  <div class="date-picker form-inline">
    <input type="hidden" name="birthday" :value="date">
    <div class="form-group col-md-4 no-padding-left">
      <label>Year</label>
      <select class="form-control year" v-model="year" @change="onChangeYear">
        <option value=""></option>
        <option v-for="year in years" :value="year">{{year}}</option>
      </select>
    </div>
    <div class="form-group col-md-4 margin-vertical-auto">
      <label>Month</label>
      <select class="form-control " v-model="month" @change="onChangeMonth">
        <option value=""></option>
        <option v-for="month in months" :value="month">{{month + 1}}</option>
      </select>
    </div>
    <div class="form-group col-md-4 no-padding-right margin-vertical-auto">
      <label>Day</label>
      <select class="form-control day" v-model="day" @change="onChangeDay">
        <option value=""></option>
        <option v-for="day in days" :value="day">{{day}}</option>
      </select>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'

  export default {
    name: 'dateSelector',
    components: [moment],
    props: {
      value: {
        type: String
      },
      format: {
        type: String,
        default: 'YYYYMMDD'
      },
      start: {
        type: String,
        default: '19000101'
      },
      default: {
        type: String,
        default: '19900101'
      },
      end: {
        type: String,
        default: '20201231'
      }
    },
    data: function () {
      return {
        year: '',
        month: '',
        day: '',
        years: [],
        months: [],
        days: [],
        startDate: null,
        endDate: null
      };
    },
    computed: {
      date: {
        get: function () {
          if (!this.month || !this.day) {
            return;
          }

          return moment({
            year: this.year,
            month: this.month,
            day: this.day
          });
        },
        set: function (v) {
          var _d = null;
          if (moment.isMoment(v)) {
            _d = v;
          } else {
            _d = moment(v, this.format);
          }
          if (!_d.isValid()) {
            throw new Error('Not valid moment or not valid time for format: ', this.format);
          }

          this.year = _d.year();
          this.$nextTick(function () {
            this.month = _d.month()
          })
          this.$nextTick(function () {
            this.day = _d.date()
          })
        }
      }
    },
    watch: {
      date: function () {
        this.$emit('input', this.date.format('MM-DD-YYYY'));
      },
      value: function () {
        if (!this.date || !moment(this.date).isSame(this.value, 'day')) {
          this.date = moment(this.value)
        }
      }
    },
    created: function () {
      var _s = moment(this.start, this.format);
      if (!_s.isValid()) {
        throw new Error('Start date is not right, or format is not right.');
      }
      var _d = moment(this.default, this.format);
      if (!_d.isValid()) {
        throw new Error('Default date is not right, or format is not right.');
      }
      var _e = moment(this.end, this.format);
      if (!_e.isValid()) {
        throw new Error('End date is not right, or format is not right.');
      }
      if (!_s.isBefore(_e)) {
        throw new Error('Start date must before end date.');
      }
      if (!_d.isBetween(_s, _e)) {
        throw new Error('Default date must between start and end.');
      }
      this.startDate = {year: _s.year(), month: _s.month(), day: _s.date()};
      this.endDate = {year: _e.year(), month: _e.month(), day: _e.date()};
      this.date = _d;
      this.getYears();
      this.getMonths();
      this.getDays();
    },
    methods: {
      getYears: function () {
        var _to = this.endDate.year;
        for (var _y = this.startDate.year; _y <= _to; _y++) {
          this.years.push(_y);
        }
      },
      getMonths: function () {
        this.months = [];
        if (this.year === '') return;
        var _to = 12;
        var _m = 1;
        if (this.year >= this.endDate.year) {
          _to = this.endDate.month;
        }
        if (this.year <= this.startDate.year) {
          _m = this.startDate.month;
        }
        for (; _m <= _to; _m++) {
          this.months.push(_m);
        }
      },
      getDays: function () {
        this.days = [];
        if (this.year === '' || this.month === '') return;
        var _to = moment(this.year + '-' + this.month, 'YYYY-MM').daysInMonth();
        var _d = 1;
        if (this.year <= this.startDate.year && this.month <= this.startDate.month) {
          _d = this.startDate.day;
        }
        if (this.year >= this.endDate.year && this.month >= this.endDate.month) {
          _to = this.endDate.day;
        }
        for (; _d <= _to; _d++) {
          this.days.push(_d);
        }
      },
      onChangeYear: function () {
        this.month = '';
        this.day = '';
        this.getMonths();
        this.getDays();
        this.$emit('date-change', this.date);
      },
      onChangeMonth: function () {
        this.day = '';
        this.getDays();
        this.$emit('date-change', this.date);
      },
      onChangeDay: function () {
        this.$emit('date-change', this.date);
      }
    }
  }
</script>

<style scoped>
  .margin-vertical-auto {
    margin-top: auto !important;
    margin-bottom: auto !important;
  }
</style>



