<template>
  <partners-searcher :partners="projectPartners"
                     removePartnerButtonText="Added"
                     :displayPermissionButtons="true"
                     @addPartner="addPermissions"
                     @removePartner="removePermissions"></partners-searcher>
</template>

<script>
  import partnersService from '@/services/partnersService'
  import PartnersSearcher from './PartnersSearcher.vue'

  export default {
    name: 'ProjectPartnersSearcher',
    props: ['folderId'],
    components: {PartnersSearcher},
    data () {
      return {
        projectPartners: []
      }
    },
    created() {
      var self = this;
      if (this.folderId) {
        partnersService.getPartnersByStorageFolder(this.folderId, function (partners) {
          if (!(partners instanceof Array)) {
            partners = [];
          }

          partners.forEach(function (partner) {
            partner.isFriend = true;
            partner.image = '/static/images/demo/users/face3.jpg';
          })

          self.projectPartners = partners;
        })
      }
    },
    methods: {
      addPermissions: function (partner, callback) {
        partnersService.addPermissionsToPartner(this.folderId, partner.username, partner.permission, callback);
      },
      removePermissions: function (partner, callback) {
        partnersService.removePermissionsFromPartner(this.folderId, partner.username, callback);
      }
    }
  }
</script>

<style scoped>

</style>



