<template>
  <div class="panel">
    <div class="panel-body border-top-primary text-center">
      <h6 class="no-margin text-semibold">Storge</h6>
      <p class="content-group-sm text-muted">your package has {{freeSpace}} GB free space left</p>

      <div class="progress content-group-sm">
        <div class="progress-bar progress-bar-primary" :data-transitiongoal-backup="storageFreePrecentage"
             :data-transitiongoal="storageFreePrecentage"
             :style="{ width: storageFreePrecentage + '%'}" :aria-valuenow="storageFreePrecentage">
          {{storageFreePrecentage}}%
        </div>
      </div>
    </div>
    <div class="panel-footer">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'StorageGague',
    data () {
      return {
        storage: {
          usedSpace: 500,
          totalSpace: 2000
        }
      }
    },

    computed: {
      freeSpace: function () {
        return this.storage.totalSpace - this.storage.usedSpace;
      },
      storageFreePrecentage: function () {
        return Math.round((this.storage.usedSpace / this.storage.totalSpace) * 100);
      }
    }
  }
</script>

<style scoped>

</style>



