<template>
  <div class="modal fade" style="display: none;">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-info">
          <button type="button" class="close" data-dismiss="modal"></button>
          <h3 class="modal-title">Add partners to project - {{projectName}}</h3>
        </div>

        <div class="modal-body">
          <project-partners-searcher :folderId="folderId"></project-partners-searcher>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-link" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ProjectPartnersSearcher from './ProjectPartnersSearcher.vue'
  export default {
    name: 'add-partners-modal',
    props: {
      projectName: '',
      addToProject: false,
      folderId: ''
    },
    data() {
      return {}
    },
    components: {ProjectPartnersSearcher}
  }
</script>

<style scoped>
  .modal-footer {
    border: none;
  }
</style>



