<template>
  <partners-searcher :partners="partners"
                     @addPartner="addPartner"
                     @removePartner="removePartner"></partners-searcher>
</template>

<script>
  import partnersService from '@/services/partnersService'
  import PartnersSearcher from './PartnersSearcher.vue'

  export default {
    name: 'UserPartnersSearcher',
    components: {PartnersSearcher},
    data () {
      return {
        partners: []
      }
    },
    created() {
      var self = this;
      partnersService.getPartners(function (partners) {
        if (!(partners instanceof Array)) {
          partners = [];
        }

        partners.forEach(function (partner) {
          partner.isFriend = true;
          partner.image = '/static/images/demo/users/face3.jpg';
        })

        self.partners = partners;
      })
    },
    methods: {
      addPartner: function (partner, callback) {
        partnersService.addPartner(partner.username, callback);
      },
      removePartner: function (partner, callback) {
        partnersService.removePartner(partner.username, callback);
      }
    }
  }
</script>

<style scoped>

</style>



