<template>

  <user-details
    :showAccountDetails="false"
    :details="details"
    title="Edit profile"
    action="update"
    @action="updateUserDetails">
  </user-details>
</template>

<script>
  import UserDetails from '../login/UserDetails.vue'
  import userService from '../../services/userService'

  export default {
    name: 'EditProfileLayout',
    components: {UserDetails},
    data () {
      return {
        details: {}
      }
    },
    created(){
      var self = this;
      userService.getCurrentUserInfo(function (data) {
        self.details = data;
      });
    },
    methods: {
      updateUserDetails(){
        var self = this;
        userService.updateUserDetails(this.details, function () {
          alert('updated!')
          self.$router.push('profile-settings');
        });
      }
    }
  }
</script>

<style scoped>

</style>



