import Vue from 'vue'
import Main from '../components/Main'
import ForgotPassword from '../components/login/ForgotPassword'
import RegistrationLayout from '../components/login/RegistrationLayout.vue'
import RegisteredSuccessfully from '../components/login/RegisteredSuccessfully'
import DashbordLayout from '../components/dashboard/DashbordLayout'
import PartnersLayout from '../components/partners/PartnersLayout'
import ProfileSettings from '../components/user/ProfileSettings'
import EditProfileLayout from '../components/user/EditProfileLayout'
import PricingLayout from '../components/package/PricingLayout'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Main
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: DashbordLayout
    },
    {
      path: '/partners',
      component: PartnersLayout
    },
    {
      path: '/forgot',
      component: ForgotPassword
    },
    {
      path: '/register',
      component: RegistrationLayout
    },
    {
      path: '/registeredSuccessfully',
      component: RegisteredSuccessfully
    },
    {
      path: '/profile-settings',
      component: ProfileSettings
    },
    {
      path: '/edit-profile',
      component: EditProfileLayout
    },
    {
      path: '/user-profile/:username',
      component: ProfileSettings,
      props: true
    },

    {
      path: '/pricing-layout',
      component: PricingLayout
    }
  ]
})



