<template>
  <ul class="nav navbar-nav navbar-right ">
    <li class="dropdown">
      <a href="#" class="dropdown-toggle login-text" data-toggle="dropdown">
        Login<i class="caret"></i>
      </a>

      <div class="dropdown-menu dropdown-content width-350">
        <div class="dropdown-content-heading">
          <div>Login
            <router-link to="/register" class="pull-right">Register</router-link>
          </div>
        </div>

        <div class="media-list dropdown-content-body">
          <form @click.prevent>
            <div class="panel panel-body login-form">
              <div class="text-center">
                <div class="icon-object border-slate-300 text-slate-300" style="padding: 10px"><i
                  class="icon-reading"></i></div>
                <h5 class="content-group">Login to your account
                  <small class="display-block">Enter your credentials below</small>
                </h5>
              </div>

              <div class="has-error" v-show="errors.any() && requestedLogin">
                <div class="alert alert-danger help-block">
                  <ul class="error-list">
                    <li v-for="error in errors.all()">{{error}}</li>
                  </ul>
                </div>
              </div>

              <fieldset>
                <div class="form-group has-feedback has-feedback-left">
                  <input type="text" class="form-control" placeholder="Username"
                         v-model.lazy="credentials.username"
                         v-validate="'required|email'" name="username"
                         style="background-image: url(&quot;data:image/png;base64,&quot;); background-repeat: no-repeat; background-attachment: scroll; background-size: 16px 18px; background-position: 98% 50%; cursor: auto;"
                         autocomplete="off">
                  <div class="form-control-feedback">
                    <i class="icon-user text-muted"></i>
                  </div>
                </div>

                <div class="form-group has-feedback has-feedback-left">
                  <input type="password" class="form-control" placeholder="Password"
                         v-model="credentials.password"
                         v-validate="'required|min:6|max:20'" name="password"
                         style="background-image: url(&quot;data:image/png;base64,&quot;); background-repeat: no-repeat; background-attachment: scroll; background-size: 16px 18px; background-position: 98% 50%; cursor: auto;"
                         autocomplete="off">
                  <div class="form-control-feedback">
                    <i class="icon-lock2 text-muted"></i>
                  </div>
                </div>
              </fieldset>

              <div class="form-group">
                <button type="submit" class="btn btn-primary btn-block" @click="login()">Sign in <i
                  class="icon-circle-right2 position-right"></i></button>
              </div>

              <div class="pull-left">
                <router-link to="/register" class="pull-right">Register</router-link>
              </div>
              <div class="text-center">
                <router-link to="/forgot" class="pull-right">Forgot password?</router-link>
              </div>
            </div>
          </form>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
  import loginService from '@/services/loginService'
  export default {
    name: 'loginDropdown',
    data () {
      return {
        credentials: {
          username: '',
          password: ''
        },
        requestedLogin: false
      }
    },
    methods: {
      login: function () {
        var self = this;
        this.requestedLogin = true;
        this.$validator.validateAll().then(function () {
          loginService.login(self.credentials, function () {
            self.$emit('loggedIn', true);
            self.$router.push('dashboard');
          }, function (serverError) {
            self.$validator.errorBag.add('serverError', serverError)
          });
        })
      }
    }
  }
</script>

<style scoped>
  .error-list {
    list-style-type: none;
    padding: 0;
  }

  .error-list > li {
    margin-bottom: 3px
  }

  .login-text {
    font-size: x-large;
    font-variant: small-caps;
    font-weight: bold;
  }
</style>



