<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="content-group ">
            <div class="panel-body bg-blue border-radius-top text-center"
                 style="background-image: url(/static/images/bg.png);
                        background-size: contain;">
              <div class="content-group-sm">
                <h1 class="text-semibold no-margin" style="font-variant-caps: small-caps;">
                  {{fullname}}
                </h1>

                <h5 class="display-block no-margin">{{userInfo.occupation}}</h5>
              </div>

              <a href="#" class="display-inline-block content-group-sm">
                <input id="file-input" type="file" name="name" style="display: none;"
                       @change="inputFile" ref="file_input"/>
                <img :src="userInfo.profilePic" class="img-circle img-responsive" alt=""
                     @click.prevent="$refs.file_input.click()"
                     style="width: 120px; height: 120px;">
              </a>
            </div>

          </div>
        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="">
          <div class="row">
            <div class="col-md-6">
              <div class="panel panel-body no-padding-bottom">
                <div class="form-group mt-5">
                  <label class="text-semibold">Full name:</label>
                  <span class="pull-right-sm">{{fullname}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Email:</label>
                  <span class="pull-right-sm">{{userInfo.email}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Company:</label>
                  <span class="pull-right-sm">{{userInfo.company}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Address:</label>
                  <span class="pull-right-sm">{{userInfo.address}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Birthday:</label>
                  <span class="pull-right-sm">{{userInfo.birthdate ? moment(userInfo.birthdate).format('DD/MM/YYYY') : ''}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Phone number:</label>
                  <span class="pull-right-sm">{{userInfo.phoneNumber}}</span>
                </div>

                <div class="form-group no-margin-bottom">
                  <label class="text-semibold">Website:</label>
                  <span class="pull-right-sm"><a :href="userInfo.website">{{userInfo.website}}</a></span>
                </div>
                <div class="panel-footer">
                  <router-link to="/edit-profile" class="btn btn-info pull-right">Edit</router-link>
                </div>
              </div>
            </div>
            <div class="col-md-6">
              <StorageGague>
                <div class="heading-elements" slot="footer">
                  <router-link to="/pricing-layout" class="btn btn-info pull-right">Upgrade</router-link>
                </div>
              </StorageGague>

              <div class="panel panel-body">
                <label class="text-semibold">Additinal info:</label>
                <p>{{userInfo.additionalInfo}}</p>
              </div>

            </div>
          </div>

          <div class="row">
            <div class="col-md-6">
              <div class="panel">
                <div class="panel-heading">
                  <h6 class="panel-title">Notifications settings</h6>
                </div>
                <div id="notifications-panel" class="panel-body border-top-primary text-center">
                  <div v-for="notification in notificationSettings" class="checkbox checkbox-right checkbox-switchery">
                    <label class="display-block ">
                      <input type="checkbox" v-model="notification.enabled">
                      <label class="pull-left">{{notification.text}}</label>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>


    </div>
    <!-- /content area -->

  </div>
  <!-- /main content -->
</template>

<script>
  import userService from '@/services/userService'
  import StorageGague from '@/components/user/StorageGague'
  import FileUpload from 'vue-upload-component'
  import moment from 'moment'

  export default {
    name: 'profile-settings',
    components: {StorageGague, FileUpload},
    props: ['username'],
    data () {
      return {
        editMode: false,
        userInfo: {
          image: '/static/images/demo/users/face4.jpg'
        },
        notificationSettings: [
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          },
          {
            text: 'Send Email notification when partner creates folder',
            enabled: false
          },
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          },
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          }
        ]
      }
    },
    created() {
      this.populateUserData();
    },
    computed: {
      fullname: function () {
        return this.userInfo.firstName + ' ' + this.userInfo.lastName
      }
    },
    methods: {
      moment,
      inputFile() {
        var self = this;
        var formData = new FormData()
        formData.append('file', this.$refs.file_input.files[0])
        userService.uploadProfilePicture(formData, function () {
          self.populateUserData();
        }, function (status, error) {
          alert(error)
        })
      },
      populateUserData() {
        var self = this;
        if (self.username) {
          userService.getUserInfo(self.username, function (data) {
            self.userInfo = data;
            if (!self.userInfo.profilePic) {
              self.userInfo.profilePic = '/static/images/demo/users/add_profile_pic.png';
            } else {
              self.userInfo.profilePic = 'data:image/jpeg;base64,' + self.userInfo.profilePic
            }
          });
        } else {
          userService.getCurrentUserInfo(function (data) {
            self.userInfo = data;
            if (!self.userInfo.profilePic) {
              self.userInfo.profilePic = '/static/images/demo/users/add_profile_pic.png';
            } else {
              self.userInfo.profilePic = 'data:image/jpeg;base64,' + self.userInfo.profilePic
            }
          });
        }
      }
    }
  }
</script>



