<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="page-header-content">
            <div class="page-title">
              <h4><a href="/"><i class="icon-arrow-left52 position-left"></i></a> <span class="text-semibold">Reset password </span>
              </h4>
              <a class="heading-elements-toggle"><i class="icon-more"></i></a></div>
          </div>

        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="content col-md-offset-2 col-md-8">

          <div class="panel panel-flat" style="padding: 0 10%;">
            <div class="panel-heading no-padding-left">
              <h5 class="panel-title">Forgot password?</h5>
            </div>

            <div class="panel-body">
              <form id="register-form" class="form-horizontal"
                    @submit.prevent="sendResetEmail()">
                <div class="row">
                  <fieldset>
                    <legend class="text-semibold"></legend>
                    <div class="form-group">
                      <label class="col-md-2 control-label">username:</label>
                      <div class="col-md-7"
                           :class="{'has-error': errors.has('username')}">
                        <input name="username" v-validate="'required|email'"
                               class="form-control" v-model="username">
                        <span v-show="errors.has('username')" class="help-block">{{ errors.first('username') }}</span>
                      </div>
                    </div>

                    <div class="text-right">
                      <button type="submit" class="btn btn-primary">Reset password <i
                        class="icon-arrow-right14 position-right"></i></button>
                    </div>
                  </fieldset>
                </div>
              </form>

              <div v-if="showNote" class="mt-20 text-success">
                A reset email was sent to your account, please check your email and click on the reset link
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ForgotPassword',
    data () {
      return {
        username: '',
        showNote: false
      }
    },
    methods: {
      sendResetEmail: function () {
        var self = this;
        this.$validator.validateAll().then(function () {
          self.showNote = true;
        });
      }
    }
  }
</script>

<style scoped>

</style>



