<template>
  <user-details
    :showAccountDetails="true"
    title="Register"
    action="Register"
    :serverError="serverError"
    @action="register">
  </user-details>
</template>

<script>
  import UserDetails from './UserDetails.vue'
  import loginService from '../../services/loginService'
  export default {
    name: 'RegistrationLayout',
    components: {UserDetails},
    data () {
      return {
        serverError: ''
      }
    },
    methods: {
      register: function (registration) {
        var self = this;
        self.serverError = '';
        loginService.register(registration, function () {
          self.$router.push('registeredSuccessfully');
        }, function (serverErrors) {
          self.serverError = serverErrors[0];
        });
      }
    }
  }
</script>

<style scoped>

</style>



